<template>
  <div class="card">
    <div class="table-responsive">
      <input type="checkbox" id="id" v-model="loadId">
      <label>统一社会信用代码</label>
      <input type="checkbox" id="name" v-model="loadName">
      <label>公司名称</label>
      <input type="checkbox" id="product" v-model="loadProduct">
      <label>产品名</label>
      <input type="checkbox" id="stock" v-model="loadStock">
      <label>股票</label>
    </div>
    <!--  展示列表  -->
    <div class="table-responsive">
      
      <table class="table table-striped">
        <thead>
          <tr>
            <th v-show="loadId">统一社会信用代码</th>
            <th v-show="loadName">公司名称</th>
            <th v-show="loadProduct">产品名</th>
            <th v-show="loadStock">股票</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="com in info.users" :key="com.id">
            <td v-show="loadId">{{com.id}}</td>
            <td v-show="loadName">{{com.title}}</td>
            <td v-show="loadProduct">{{com.product}}</td>
            <td v-show="loadStock">{{ com | outputStock }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <!--  展示加载中  -->
    <div>
      <p v-show="info.isLoading">稍等一下喵喵</p>
    </div>
    <!-- 展示错误 -->
    <div>
      <p v-show="info.errMsg">{{info.errMsg}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'lList',
  data() {
    return {
      info:{
        isLoading: false,
        errMsg: '',
        users: [],
      },
      // loadId: this.$route.query.id == 'true' || false,
      // loadName: this.$route.query.name == 'true' || false,
      // loadProduct: this.$route.query.product == 'true' || false,
      // loadStock: this.$route.query.stock == 'true' || false
      loadId: false,
      loadName: true,
      loadProduct: true,
      loadStock: false
    }
  },
  mounted(){
    this.$bus.$on('updateData', (dataObj)=>{this.info = {...this.info, ...dataObj}})
    
  },
  filters: {
    outputStock(value){
      let str = ''
      if (value.stock_hk) str += '港股：' + value.stock_hk + ' '
      if (value.stock_A) str += 'A股：' + value.stock_A
      if (value.stock_nasdaq) str += '美股：' + value.stock_nasdaq
      return str
    }
  },
  // computed:{
  //   watchLoad(){
  //     return {id: this.loadId, name: this.loadName, product: this.loadProduct, stock: this.loadStock}
  //   }
  // },
  // watch:{
  //   watchLoad(value) {
  //     this.$router.replace({
  //       name: 'list',
  //       query: value
  //     })
  //   }
  // }
}
</script>

