import VueRouter from 'vue-router'
import sSearch from '../components/Search.vue'
import lList from '../components/List.vue'
// import App from '../App.vue'

export default new VueRouter({
  mode: "history",
  routes: [
    {
      path: '/',
      component: sSearch,
      name: 'search'
      
    },
    {
      path: '/',
      component: lList,
      name: 'list'
    }
  ]
})