<template>
  <section class="jumbotron">
    <h2 class="sub-header">用于快速查询的表单</h2>
    <div class="container">
      <input type="text" placeholder="请输入检索内容" v-model="keyWord" class="form-control">
      <!-- <button @click="getCompanies(); pushSearch()" class="btn-lg btn-primary btn-block">搜索</button> -->
      <button @click=getCompanies class="btn-lg btn-primary btn-block">搜索</button>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
// import companies from '../end.json'
export default {
  name: 'sSearch',
  data() {
    return {
      keyWord: this.$route.query.keyword || '',
      companies: {},
      usersData: {}
    }
  },
  mounted(){
    this.initData()
  },
  methods: {
    getCompanies(){
      this.$bus.$emit('updateData', {isLoading: true, users: [], errMsg: ''})
      this.usersData = this.companies.company.filter(x => (x.title.includes(this.keyWord)) || (x.category.includes(this.keyWord)) || (x.product.includes(this.keyWord)))
      this.$bus.$emit('updateData', {isLoading: false, users: this.usersData, errMsg: ''})


    },
    initData(){
      axios.get(`./end.json`).then(
        response => {

          this.companies = Object.assign(this.companies, response.data)
          this.getCompanies()
        },
        error => {
          this.$bus.$emit('updateData', {users: '', errMsg: error.message})
        }
      )
    },
    // pushSearch(){
    //   if (this.keyWord == this.$route.query.keyword) return 
    //   this.$router.replace({
    //     name: 'search',
    //     query: {
    //       keyword: this.keyWord
    //     }
    //   })
    // }

  },
}
</script>

<style scoped>
.container{
  display: flex;
  flex-direction: row;
  width: 100vw;
}
input{
  width: 70vw;
}
h2{
  text-align: center;
}
</style>